@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}
.form-container {
  margin: 100px auto;
  width: 30vw;
  box-shadow: 0 5px 8px 0 rgba(17, 59, 70, 0.2), 0 7px 20px 0 rgba(21, 32, 83, 0.2);
  position: relative;
  border-radius: 10px;
  height: 80vh;
  display: grid;
   
  
  grid-template-columns: 1fr;
  overflow: hidden;
}

@media screen and (max-width:320px){
  .form-container {
    width: 100vw;
    padding:2px;
    height: 90vh;
    
  }
}

@media screen and (max-width:1024px){
  .form-container {
    width: 80vw;
   
  }
}

.close-btn {
  position: absolute;
  top: 2%;
  right: 3%;
  font-size: 1.5rem;
  z-index: 1;
  color: #fff;
  cursor: pointer;
}

